import React, { useState , useEffect } from 'react'
import './Nav.css'
import { BsInstagram } from "react-icons/bs";
import { AiOutlineMail } from "react-icons/ai";
import { AiOutlineFacebook } from "react-icons/ai";
import { BsWhatsapp } from "react-icons/bs";
import { FiTwitter } from "react-icons/fi";
import { Link } from 'react-router-dom';


      


function Nav() {
    const [Show,handleShow]=useState(false);

    const transNav = () =>{
        if (window.scrollY > 100 ){
            handleShow(true);
        }
        else{
            handleShow(false);
        }
    };
    useEffect(() => {
        window.addEventListener("scroll", transNav);
        return () => {
            window.removeEventListener("scroll", transNav);
        }
    }, []);
     const [state, setstate] = useState(false)
 const Showdropdown=()=>{
        setstate(true);
 }
 const Hidedropdown=()=>{
     setstate(false);

 }
    return (
        <section id="home">
    <div className={`Nav ${Show && "Nav_yellow"}`}>
        <div className="Nav_contents">
            <div className="Nav_icons">
           <span className="Insta"> <BsInstagram/></span>
           <span className="Mail"> <AiOutlineMail/></span>
           <span className="Facebook"> <AiOutlineFacebook/></span>
           <span className="Whatsapp"> <BsWhatsapp/></span>
           <span className="Twitter"> <FiTwitter/></span>
            </div>
        
                <img className="Nav_logo" src= "../censcaff logo (1).png" alt=""/>
        
        <div className="Nav_pages">
             <span className="Home">
                 <a className="smoothscroll" href="#home">
            Home
            </a>
           
             </span>
              <span className="About Us"> 
               <a className="smoothscroll" href="#aboutus">
              About Us
            </a>
              </span>
               <span className="Products"> 
               <a className="smoothscroll" href="#product">

            </a>
               </span>


 
    
        <div className="dropdown">
            <div className="dropdownmenu" onMouseEnter={Showdropdown} onMouseLeave={Hidedropdown} >
Products              {state?(  <ul onMouseEnter={Showdropdown}>
                 
                    <li>
                       <Link className="nav-link" to = "/Cuplock scaffolding">Cuplock scaffolding </Link>
                    </li>
                    <li>
                         <Link className="nav-link" to = "/Adjustable props">Adjustable props</Link>
                    </li>
                    <li>
                        <Link className="nav-link" to = "/Centering sheet">Centering sheet</Link>
                    </li>
                     <li>
                        <Link className="nav-link" to = "/Telescopic span">Telescopic span</Link>
                    </li>
                </ul>):
                null}

            </div>

            
        </div>
    


                <span className="Contact Us"> 
                <a className="smoothscroll" href="#contactus">
              Contact Us
            </a>
                </span>
             
           
            
        </div>
        
        </div>

           
     </div>
     </section>
    );
}

export default Nav
