import React from 'react'

import'./Banner.css'

function Banner() {
    return (
             <section id="Form">      
            <header className="Banner" style={{
            backgroundSize: "cover",
            backgroundImage: `url("https://static9.depositphotos.com/1010826/1179/v/600/depositphotos_11791918-stock-illustration-tower-cranes.jpg")`,
            backgroundPosition: "center center",}}>
                
            <div className="Banner_contents">
                
             <h1 className="Banner_title">Premium Construction Equipments For Hire</h1>
               <span className="Enquire">
                 <a className="smoothscroll" href="#form">
          <button className="Banner_button">  Enquire now</button>
            </a></span>
            
            </div>
            
            <div className="banner--fadebottom" />
        </header>
        </section>
        
    )
}

export default Banner
